type LogLevel = 'info' | 'warn' | 'error' | 'debug'

interface SupabaseLogEntry {
  timestamp: string
  level: LogLevel
  operation: string
  table?: string
  userId?: string
  duration?: number
  details?: any
  error?: any
}

class SupabaseLogger {
  private logs: SupabaseLogEntry[] = []
  private maxLogs = 100 // Keep only last 100 logs

  private formatTimestamp(): string {
    return new Date().toISOString()
  }

  private addLog(entry: SupabaseLogEntry) {
    this.logs.push(entry)
    
    // Keep only the most recent logs
    if (this.logs.length > this.maxLogs) {
      this.logs = this.logs.slice(-this.maxLogs)
    }

    // Console output with appropriate styling
    const emoji = {
      info: 'â„¹ï¸',
      warn: 'âš ï¸',
      error: 'âŒ',
      debug: 'ðŸ”'
    }

    const prefix = `${emoji[entry.level]} [SUPABASE ${entry.level.toUpperCase()}]`
    const message = `${prefix} ${entry.operation}`
    
    switch (entry.level) {
      case 'error':
        console.error(message, entry.details || entry.error)
        break
      case 'warn':
        console.warn(message, entry.details)
        break
      case 'debug':
        console.debug(message, entry.details)
        break
      default:
        console.log(message, entry.details)
    }
  }

  // Log successful database operations
  logOperation(operation: string, table: string, details?: any, duration?: number) {
    this.addLog({
      timestamp: this.formatTimestamp(),
      level: 'info',
      operation: `${operation} on ${table}`,
      table,
      duration,
      details
    })
  }

  // Log authentication events
  logAuth(event: string, userId?: string, details?: any) {
    this.addLog({
      timestamp: this.formatTimestamp(),
      level: 'info',
      operation: `Auth: ${event}`,
      userId,
      details
    })
  }

  // Log errors with context
  logError(operation: string, error: any, context?: any) {
    this.addLog({
      timestamp: this.formatTimestamp(),
      level: 'error',
      operation: `Error: ${operation}`,
      error,
      details: context
    })
  }

  // Log warnings
  logWarning(operation: string, message: string, details?: any) {
    this.addLog({
      timestamp: this.formatTimestamp(),
      level: 'warn',
      operation,
      details: { message, ...details }
    })
  }

  // Log debug information (only in development)
  logDebug(operation: string, details?: any) {
    if (process.env.NODE_ENV === 'development') {
      this.addLog({
        timestamp: this.formatTimestamp(),
        level: 'debug',
        operation,
        details
      })
    }
  }

  // Log connection events
  logConnection(status: 'connecting' | 'connected' | 'disconnected' | 'failed', details?: any) {
    const level: LogLevel = status === 'failed' ? 'error' : status === 'disconnected' ? 'warn' : 'info'
    
    this.addLog({
      timestamp: this.formatTimestamp(),
      level,
      operation: `Connection ${status}`,
      details
    })
  }

  // Get recent logs
  getRecentLogs(count: number = 10): SupabaseLogEntry[] {
    return this.logs.slice(-count)
  }

  // Get logs by level
  getLogsByLevel(level: LogLevel): SupabaseLogEntry[] {
    return this.logs.filter(log => log.level === level)
  }

  // Clear logs
  clearLogs() {
    this.logs = []
    console.log('ðŸ§¹ Supabase logs cleared')
  }

  // Export logs for debugging
  exportLogs(): string {
    return JSON.stringify(this.logs, null, 2)
  }

  // Performance monitoring
  startTimer(operation: string): () => void {
    const startTime = performance.now()
    
    return () => {
      const duration = performance.now() - startTime
      this.logDebug(`Performance: ${operation}`, { duration: `${duration.toFixed(2)}ms` })
      return duration
    }
  }
}

// Singleton instance
export const supabaseLogger = new SupabaseLogger()

// Convenience functions
export const logSupabaseOperation = (operation: string, table: string, details?: any, duration?: number) => {
  supabaseLogger.logOperation(operation, table, details, duration)
}

export const logSupabaseAuth = (event: string, userId?: string, details?: any) => {
  supabaseLogger.logAuth(event, userId, details)
}

export const logSupabaseError = (operation: string, error: any, context?: any) => {
  supabaseLogger.logError(operation, error, context)
}

export const logSupabaseWarning = (operation: string, message: string, details?: any) => {
  supabaseLogger.logWarning(operation, message, details)
}

export const logSupabaseDebug = (operation: string, details?: any) => {
  supabaseLogger.logDebug(operation, details)
}

export const logSupabaseConnection = (status: 'connecting' | 'connected' | 'disconnected' | 'failed', details?: any) => {
  supabaseLogger.logConnection(status, details)
}

// Hook for React components to access logs
export const useSupabaseLogs = () => {
  return {
    getRecentLogs: (count?: number) => supabaseLogger.getRecentLogs(count),
    getLogsByLevel: (level: LogLevel) => supabaseLogger.getLogsByLevel(level),
    clearLogs: () => supabaseLogger.clearLogs(),
    exportLogs: () => supabaseLogger.exportLogs()
  }
}

export default supabaseLogger
