import { supabase } from './supabase';
import type { User } from '@/types/database';

export interface AuthUser {
  id: string;
  phone: string;
  name: string;
  role: string;
}

// Simple authentication service using phone number and common password
export const authService = {
  // Login with phone number and password
  async login(phone: string, password: string): Promise<{ user: AuthUser | null; error: string | null }> {
    try {
      // For now, we're using a common password "root" for all citizens
      if (password !== 'root') {
        return { user: null, error: 'Invalid password' };
      }

      console.log('Attempting login for phone:', phone);

      // Automatically add 91 country code if not present
      const phoneWithCountryCode = phone.startsWith('91') ? phone : `91${phone}`;
      console.log('Phone with country code:', phoneWithCountryCode);

      // Find user by phone number
      const { data: users, error } = await supabase
        .from('users')
        .select('*')
        .eq('phone_number', phoneWithCountryCode)
        .limit(1);

      console.log('Database query result:', { users, error });

      if (error) {
        console.error('Auth error:', error);
        return { user: null, error: `Authentication failed: ${error.message}` };
      }

      if (!users || users.length === 0) {
        return { user: null, error: 'User not found. Please check your phone number.' };
      }

      const user = users[0];
      console.log('Found user:', user);

      const authUser: AuthUser = {
        id: user.id,
        phone: user.phone_number,
        name: user.name,
        role: user.role
      };

      // Store user in localStorage for simple session management
      localStorage.setItem('currentUser', JSON.stringify(authUser));

      return { user: authUser, error: null };
    } catch (error) {
      console.error('Login error:', error);
      return { user: null, error: 'An unexpected error occurred' };
    }
  },

  // Get current authenticated user
  getCurrentUser(): AuthUser | null {
    try {
      const userStr = localStorage.getItem('currentUser');
      if (!userStr) return null;
      return JSON.parse(userStr);
    } catch {
      return null;
    }
  },

  // Logout user
  logout(): void {
    localStorage.removeItem('currentUser');
  },

  // Check if user is authenticated
  isAuthenticated(): boolean {
    return this.getCurrentUser() !== null;
  }
};
