import { createClient, SupabaseClient } from '@supabase/supabase-js'
import { logSupabaseConnection, logSupabaseError, logSupabaseDebug } from './supabase-logger'
import type { Database } from '@/types/database'

// Environment variables
const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!

// Validate environment variables
if (!supabaseUrl) {
  const errorMsg = 'Missing NEXT_PUBLIC_SUPABASE_URL'
  logSupabaseError('initialization', new Error(errorMsg))
  throw new Error(errorMsg)
}

if (!supabaseAnonKey) {
  const errorMsg = 'Missing NEXT_PUBLIC_SUPABASE_ANON_KEY'
  logSupabaseError('initialization', new Error(errorMsg))
  throw new Error(errorMsg)
}

// Log connection attempt
logSupabaseConnection('connecting', {
  url: supabaseUrl,
  keyPreview: supabaseAnonKey.substring(0, 20) + '...'
})

// Create Supabase client
export const supabase: SupabaseClient<Database> = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    persistSession: true,
    autoRefreshToken: true,
    detectSessionInUrl: true
  },
  db: {
    schema: 'public'
  },
  global: {
    headers: {
      'X-Client-Info': 'Nirvana-Municipal-Platform'
    }
  }
})

// Connection validation function
export const validateSupabaseConnection = async (): Promise<boolean> => {
  try {
    logSupabaseDebug('Starting connection validation')
    
    // Test the connection by making a simple query
    const { data, error } = await supabase
      .from('_health_check')
      .select('*')
      .limit(1)

    if (error) {
      // If health check table doesn't exist, try a different approach
      if (error.code === 'PGRST116' || error.message.includes('does not exist')) {
        logSupabaseDebug('Health check table not found, testing with auth instead')
        
        // Test with auth session
        const { data: session, error: authError } = await supabase.auth.getSession()
        
        if (authError) {
          logSupabaseError('auth_connection_test', authError)
          return false
        }
        
        logSupabaseConnection('connected', {
          method: 'auth_test',
          hasActiveSession: !!session.session
        })
        return true
      } else {
        logSupabaseError('health_check', error)
        return false
      }
    }

    logSupabaseConnection('connected', {
      method: 'health_check',
      responseData: data
    })
    return true

  } catch (error) {
    logSupabaseError('connection_validation', error)
    return false
  }
}

// Auto-validate connection on import (only in development)
if (process.env.NODE_ENV === 'development') {
  validateSupabaseConnection().then((isValid) => {
    if (isValid) {
      logSupabaseConnection('connected', { autoValidation: true })
    } else {
      logSupabaseConnection('failed', { autoValidation: true })
    }
  })
}

// Export types for TypeScript
export type { SupabaseClient } from '@supabase/supabase-js'
export type { Database } from '@/types/database'

// Helper function to log database operations (deprecated - use logger directly)
export const logDatabaseOperation = (operation: string, table: string, details?: any) => {
  logSupabaseDebug(`Database Operation: ${operation.toUpperCase()} on ${table}`, details)
}

// Helper function to handle Supabase errors with logging
export const handleSupabaseError = (error: any, context: string) => {
  logSupabaseError(context, error)
  
  // Return user-friendly error message
  return {
    error: true,
    message: error.message || 'An unexpected error occurred',
    code: error.code
  }
}

export default supabase
