import { supabase } from './supabase';

// Debug utility to check database connection and see available users
export const debugService = {
  // Test database connection
  async testConnection(): Promise<{ success: boolean; message: string }> {
    try {
      const { data, error } = await supabase
        .from('users')
        .select('count')
        .limit(1);

      if (error) {
        return { success: false, message: `Connection failed: ${error.message}` };
      }

      return { success: true, message: 'Database connection successful' };
    } catch (error) {
      return { success: false, message: `Unexpected error: ${error}` };
    }
  },

  // List all users (phone numbers only for privacy)
  async listUsers(): Promise<{ users: any[] | null; error: string | null }> {
    try {
      const { data: users, error } = await supabase
        .from('users')
        .select('id, name, phone_number, role')
        .order('name', { ascending: true });

      if (error) {
        console.error('Error fetching users:', error);
        return { users: null, error: error.message };
      }

      return { users, error: null };
    } catch (error) {
      return { users: null, error: `Unexpected error: ${error}` };
    }
  },

  // Search for a specific phone number
  async findUserByPhone(phone: string): Promise<{ user: any | null; error: string | null }> {
    try {
      const { data: users, error } = await supabase
        .from('users')
        .select('*')
        .eq('phone_number', phone);

      if (error) {
        return { user: null, error: error.message };
      }

      return { user: users && users.length > 0 ? users[0] : null, error: null };
    } catch (error) {
      return { user: null, error: `Unexpected error: ${error}` };
    }
  }
};
